<template>
  <div style="width:100%;height:100%">
    <div class="menu-top">账号管理</div>
    <div class="table-top radius_8">
      <div class="table-top-left">
        <div class="table-top-left-top">
          <div class="div_p">
            <span class="font_size_20">账号</span>
            <el-input class="inp" placeholder="请输入内容" v-model="sel.saller_tel"></el-input>
          </div>
          <div class="div_p">
            <span class="font_size_20">创建时间</span>
            <el-date-picker class="inp" v-model="sel.create_time" type="date" placeholder="选择日期"></el-date-picker>
          </div>
          <div class="div_p">
            <span class="font_size_20"></span>
            <div class="inp"></div>
          </div>
        </div>
        <div class="table-top-left-btn">
          <div class="div_p">
            <span class="font_size_20">团队</span>
            <el-select class="inp" v-model="sel.team_name" placeholder="请选择">
              <el-option v-for="(item,index) in options" :key="index" :label="item==null?'全部':item" :value="item"></el-option>
            </el-select>
          </div>
          <div class="div_p">
            <span class="font_size_20">名字</span>
            <el-input class="inp" v-model="sel.saller_name" placeholder="请输入内容"></el-input>
          </div>
          <div class="div_p">
            <span class="font_size_20"></span>
            <div class="inp"></div>
          </div>
        </div>
      </div>
      <div class="table-top-rig">
        <el-button @click="reset()" class="but">重置</el-button>
        <el-button @click="changeDate()" class="but" type="primary">查询</el-button>
      </div>
    </div>

    <div class="menu-small-top">
      <span class="font_size_weight_600" >账号列表</span>
      <el-button class="but" @click="add" style="float:right" type="primary">添加</el-button>
    </div>
    <div class="table-btn">
      <template>
        <el-table :data="tableData" style="width: 100%" class="font_size_16">
          <!-- type="index" :index="indexMethod" -->
          <el-table-column label="序号" width="100" align="center" prop="NO"></el-table-column>
          <!-- <el-table-column  width="80" align="center" prop="NO"></el-table-column> -->
          <el-table-column prop="saller_tel" label="账号" width="230"></el-table-column>
          <el-table-column prop="create_time" label="创建时间" width="230"></el-table-column>
          <el-table-column prop="saller_name" width="230" label="名字"></el-table-column>
          <el-table-column prop="title" width="230" label="角色"></el-table-column>
          <el-table-column prop="team_name" width="230" label="团队"></el-table-column>
          <el-table-column prop="leader" width="230" label="直系领导"></el-table-column>
          <!-- <el-table-column
                prop="address"
                 width="200"
                label="项目">
          </el-table-column>-->

          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button class="font_size_14" @click="set(scope.row)" type="text" size="small">编辑</el-button>
              <el-button class="font_size_14" @click="del(scope.row)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <div   class="page">
        <el-pagination
          background
          layout="total,prev, pager, next,jumper"
          @current-change="handleCurrentChange"
          :page-size="sel.pre_page"
          :total="total"
        ></el-pagination>
      </div>
      <!--:page-count="count" -->
    </div>
    <el-dialog 
     :modal-append-to-body='false'
    :title="tabName" :visible.sync="isDia" width="420px" :before-close="closeSet">
      <el-form  :model="saller" :rules="rules" ref="ruleForm" label-width="130px" class="demo-ruleForm">
          <el-form-item label="名字" prop="saller_name">
              <el-input :disabled="dis" class="inp" v-model="saller.saller_name" placeholder="请输入"></el-input>
          </el-form-item>
           <el-form-item label="账号" prop="saller_tel">
               <el-input :disabled="dis" class="inp" v-model="saller.saller_tel" placeholder="请输入"></el-input>
           </el-form-item>
            <el-form-item 
                v-if="tabName =='修改'"
                label="密码" >
               <el-input class="inp" v-model="saller.password" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item 
                v-else
                label="密码" 
                prop="password"
                >
               <el-input class="inp" v-model="saller.password" placeholder="请输入"></el-input>
            </el-form-item>
            
            <el-form-item label="选择角色" >
               <div class="set-role-right">
                 <template>
                  <el-radio-group v-model="saller.title" :disabled="isTitleDirector" :change="chgTit()">
                    <el-radio label="销售总监">销售总监</el-radio>
                    <el-radio label="销售经理">销售经理</el-radio>
                    <el-radio label="销售顾问">销售顾问</el-radio>
                    <el-radio label="女专">女专</el-radio>
                    <el-radio label="财务专员">财务专员</el-radio>
                  </el-radio-group>
                 </template>
               </div>
          </el-form-item>
         <el-form-item label="团队" >
              <el-select class="inp" v-model="saller.team_name" placeholder="请选择" :disabled="isTeamDirector">
                  <el-option v-for="(item,index) in options" :key="index" :label="item==null?'请选择':item" :value="item"></el-option>
              </el-select>  
           </el-form-item>
      </el-form>
      <!-- <div class="upPaw">
        <div class="upPaw_pa">
          <span>名字</span>
          </div>
        <div class="upPaw_pa">
          <span>账号</span>
          <el-input :disabled="dis" class="inp" v-model="saller.saller_tel" placeholder="请输入"></el-input>
        </div>
        <div class="upPaw_pa">
          <span>密码</span>
          <el-input class="inp" v-model="saller.password" placeholder="请输入"></el-input>
        </div>
        <div class="set-role">
          <span>选择角色</span>
          <div class="set-role-right">
            <template>
              <el-radio-group v-model="saller.title" :disabled="isTitleDirector" :change="chgTit()">
                <el-radio label="销售总监">销售总监</el-radio>
                <el-radio label="销售经理">销售经理</el-radio>
                <el-radio label="销售顾问">销售顾问</el-radio>
                <el-radio label="女专">女专</el-radio>
                <el-radio label="财务专员">财务专员</el-radio>
              </el-radio-group>
            </template>
          </div>
        </div>
        <div class="upPaw_pa">
          <span>团队</span>
          <el-select class="inp" v-model="saller.team_name" placeholder="请选择" :disabled="isTeamDirector">
            <el-option v-for="(item,index) in options" :key="index" :label="item==null?'请选择':item" :value="item"></el-option>
          </el-select>
        </div>
      </div> -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeSet">取 消</el-button>
        <el-button type="primary" @click="upOk('ruleForm')" :loading="isLoad">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  
  getAccounts,
  selectGroups,
  addGroup,
  delGroup,
  updateGroup,
  getGroup
} from "@/api/getData";
import { setStore, getStore } from "@/config/mUtils";
export default {
  name: "cus",

  data() {
    return {
      isTeamDirector:false,
      isTitleDirector:false,
      user:null,
      // 可修改不可修改
      dis:false,
      isLoad: false,
      // count: 10, //总页数
      total: 100, //总条数
      value: "",
      radio: "",
      time: "",
      isDia: false,
      sel: {
        saller_tel: null,
        create_time: null,
        saller_name: null,
        team_name: null,
        pre_page: 10, //每页条数
        page_num: 1 //第几页
      },
      tabName: "创建账号",
      saller: {
        saller_id:null,
        saller_name: null,
        saller_tel: null,
        password: null,
        title: "销售总监",
        team_name: null
      },
      rules:{
          saller_name: [
            { required: true, message: '名字不可为空', trigger: 'blur' },
           
          ],
           saller_tel: [
            { required: true, message: '账号不可为空', trigger: 'blur' },
           
          ],
          password: [
            { required: true, message: '密码不可为空', trigger: 'blur' },
           
          ],
      },
      options: [],

      tableData: []
    };
  },
  components: {},
  computed: {},
  async created() {
     this.getUser();
    // this.handleListener();
    await this.changeDate();
  
   
  },
  async mounted() {
    await this.chanTeam();
  },

  activated() {
    // this.handleListener();
  },
  deactivated() {
    // window.removeEventListener('resize', this.renderChart);
    // bus.$off('collapse', this.handleBus);
  },
  methods: {
    getUser(){
       var data = getStore("user");
       var jsondata = data ? JSON.parse(data) : {};
       this.user=jsondata;
    },
    // 初始化
    init() {
      this.isLoad = false;
      this.saller = {
       saller_id:null,
        saller_name: null,
        saller_tel: null,
        password: null,
        title: "销售总监",
        team_name: null
      };
    },
    async changeDate() {
   


      var dt = JSON.stringify(this.sel);
      var jsonDt = JSON.parse(dt);

      // 改变时间格式
      if (jsonDt.create_time) {
        var getTime = new Date(jsonDt.create_time);
        jsonDt.create_time = getTime.toLocaleDateString();
      }

      // 查询数据
      const res = await getAccounts(jsonDt);
      // console.log(res);
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.tableData = res.data.accounts;
      this.total = res.data.total;
    },
    indexMethod(index) {
      return index + 1 * 1;
    },
    chgTit(){
  
      if (this.saller.title=='销售顾问') {
        this.isTeamDirector=false;
      }
      else{
          this.isTeamDirector=true;
          this.saller.team_name=null;
      }
    },
    handleCurrentChange(val) {
      //页数发生变化
  // 顾问添加修改
      // alert(`当前页: ${val}`);
      this.sel.page_num = val;
      this.changeDate();
    },
   async set(ent) {
      this.tabName = "修改";
    
      this.isTitleDirector=true;
      this.dis=true;
      const res = await getGroup({ saller_id: ent.saller_id });
      this.saller=res.data;
      this.isDia = true;
      this.isLoad=false;
    
      if (ent.title=='销售顾问') {
        this.isTeamDirector=false;
      }
      else{
           this.isTeamDirector=true;
      }
    },
    async del(ent) {
      this.$confirm("您确定要删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async () => {
        const res = await delGroup({ saller_id: ent.saller_id });
        // console.log(jsondata['build_id'] )
        if (res.code != 0) {
          this.$message({
            type: "error",
            message: res.msg
          });
          return;
        }
        this.$message({
          type: "success",
          message: "删除成功!"
        });
        this.changeDate();
      });
    },
    add() {
     
      this.isDia = true;
      this.isTeamDirector=true;
      this.isTitleDirector=false;
      this.tabName = "添加";
  
      this.init();
      this.dis=false;
     
    },
    closeSet() {
      
      this.isDia = false;
      if (this.tabName=='添加') {
         this.$refs['ruleForm']?this.$refs['ruleForm'].resetFields():'';
      }
     
    },
    async upOk(formName) {
       this.$refs[formName].validate(async (valid) => {
        if(valid){
          var regex = /^(13[0-9]{9})|(15[0-9]{9})|(17[0-9]{9})|(18[0-9]{9})|(19[0-9]{9})$/;
          if (!this.saller.saller_name) {
            this.$message({
              type: "error",
              message: "名称不可为空"
            });
            return;
          }
          if (!this.saller.saller_tel) {
            this.$message({
              type: "error",
              message: "账号不可为空"
            });
            return;
          }

          if (!regex.test(this.saller.saller_tel)) {
            this.$message({
              type: "error",
              message: "账号格式有误"
            });
            return;
          }
      
          this.isLoad = true;
          let res ;
          if (this.tabName == "添加") {
            if (!this.saller.password) {
            this.$message({
              type: "error",
              message: "密码不可为空"
            });
            return;
          }
            res = await addGroup(this.saller);
          } else {
            res = await updateGroup(this.saller);
          }

          this.isLoad = false;
        
          // console.log(jsondata['build_id'] )
          if (res.code != 0) {
            this.$message({
              type: "error",
              message: res.msg
            });

            return;
          }
          this.$message({
            type: "success",
            message: "设置成功"
          });
          this.init();
          this.changeDate();
          this.isDia = false;
          
        }
       });
    },
    reset() {
      var te = this.sel;
      // console.log(te)
      te.saller_tel = null;
      te.create_time = null;
      te.saller_name = null;
      te.team_name = null;
      this.sel = te;
    },
    // 改变团队
    async chanTeam() {
  
      const res = await selectGroups({ build_id: this.user["build_id"] });
      // console.log(jsondata['build_id'] )
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      if (Array.isArray(res.data)){
         this.options = res.data;
      }
      else{
         this.options=[];
      }

      this.options.unshift(null);
      // console.log(res);
    }
  }
};
</script>


<style scoped>
.grid-content {
  background-color: black;
}
.table-top {
  display: flex;
  justify-content: space-between;
}
.table-top-left {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-top-rig {
  width: 17%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.table-top-left-top,
.table-top-left-btn {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.div_p {
  width: 26%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inp {
  width: 200px;
}

.page {
  display: flex;
  height: 38px;
  margin-right: 10px;
  align-items: center;
  flex-direction: row-reverse;

  /* float: right; */
}
/* 修改 */
.upPaw {
  width: 90%;
  margin-left: 4%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.upPaw div span {
  font-size: 20px;
  /* font-weight: 600; */
  width: 35%;
}
.upPaw_pa {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}
.set-role {
  margin-top: 20px;
  width: 100%;
  height: 30%;
  /* background: rosybrown; */
  display: flex;
}
.set-role span {
}
.set-role-right {
  vertical-align: middle;
  width: 200px;
  /* background: royalblue; */
}
.set-role-right >>> .el-radio {
  margin-top: 5px;
  margin-right: 20px ;
}
</style>
<style >
.el-table__header {
  padding: 5px 0;
}
.el-table__body-wrapper .el-table__row td {
  padding: 5px 0;
}
.el-input__inner {
  height: 37px;
}
.el-input__icon {
  line-height: 37px;
}


 .div_p .el-date-editor.el-input {
    width: 200px;
}
</style>



